/*CORE SECTION*/

/*Font face*/
@import url('https://fonts.googleapis.com/css2?family=Quicksand&display=swap');

/*Color variables*/
:root {
    --white-base : #ECECEC;
    --black-base : #222831;
    --blue-base : #30475E;
    --orange-base : #F2A365;
}

html, body {
    scroll-behavior: smooth;
    padding: 0;
    margin: 0;
    overflow-x: hidden;
    font-family: 'Quicksand', sans-serif;
}


.title-black {
    color: var(--black-base);
}

*{
    font-family:'Quicksand', sans-serif;
    letter-spacing: 1.3px;
}

.hr-white {
    border-bottom: 2px solid var(--white-base);
}

.links {
    text-decoration: underline;
    color: var(--black-base);
}

.links:hover {
    color: var(--blue-base);
}

.links-white {
    text-decoration: underline;
    color: var(--white-base);
}

.links-white:hover {
    color: var(--blue-base);
}

.img-logo-social {
    width: 25px;
    height: 25px;
}

.img-logo-about {
    width: 30px;
    height: 30px;
    filter: invert();
}

.img-logo-tech {
    width: 35px;
    height: 35px;
}

.default-wrapper {
    margin: 0;
    padding: 20px;
}

.scroll-to-top-btn {
    background-color: var(--blue-base);
    border: 1px solid var(--white-base);
    color: var(--white-base);
    border-radius: 50%;
    width: 50px;
    height: 50px;
    position: fixed;
    bottom: 10px; 
    right: 20px; 
    z-index: 99; 
    font-size: 20px;
    padding-bottom: 5px;
    transition: 0.4s;
}

.scroll-to-top-btn:hover {
    background-color: var(--black-base);
    transform: rotateZ(360deg);
}

/*SCROLLBAR*/

/* Width */
::-webkit-scrollbar {
    width: 7px;
}

/* Background */
::-webkit-scrollbar-track {
    background: var(--black-base);
}

/*The Scrollbar itself*/
::-webkit-scrollbar-thumb {
    background: var(--blue-base);
}
  
::-webkit-scrollbar-thumb:hover {
    background: var(--white-base);
}


/*NAVBAR SECTION*/

nav { 
    box-shadow: 0px 10px 15px 10px rgb(0 0 0 / 15%);
    background-color: var(--black-base); 
    border-bottom: 2px solid var(--white-base);
    color: var(--white-base);
    text-decoration: none;
    z-index: 2;
}

.navbar-brand {
    color: var(--white-base);
    background-image: linear-gradient(
        transparent 0%,
        transparent 90%,
        #E4E4E4 90%,
        #E4E4E4 100%
    );
    background-repeat: no-repeat;
    background-size: 0% 100%;
    background-position-x: right;
    transition: background-size 800ms;
    margin-left: 30px;
}

.navbar-brand:hover {
    cursor: pointer;
    background-size: 100% 100%;
    background-position-x: left;
    color: var(--white-base);
}

.nav-item .nav-link {
    color: var(--white-base);
    text-decoration: none;
    transition: ease-out 0.2s;
    margin: 0px 10px 0px 10px;
    background-image: linear-gradient(
        transparent 0%,
        transparent 90%,
        #E4E4E4 90%,
        #E4E4E4 100%
    );
    background-repeat: no-repeat;
    background-size: 0% 100%;
    background-position-x: right;
    transition: background-size 800ms;
}

.navbar-nav .nav-link:hover {
    color: var(--white-base);
    cursor: pointer;
    background-size: 100% 100%;
    background-position-x: left;
}

.dropdown-menu-toggle {
    width: 25px;
    height: 25px;
    filter: invert();
}

.dropdown {
    background-color: var(--blue-base);
    color: var(--white-base);
    border-radius: 5px;
}

/*LANDING PAGE SECTION*/

.section-1 {
    height: 100vh;
    text-align: center;
    background-color: var(--blue-base);
}

.s1-wrapper {
    display: flex;
    flex-direction: row;
    gap: 0;
}

.s1-left-side {
    padding: 50px;
    color: var(--white-base);
    width: 65%;
    height: 100vh;
    margin: 0 auto;
    text-align: left;
}

.greeting {
    margin-top: 25vh;
    z-index: 2;
}

.s1-right-side {
    padding: 50px;
    color: var(--white-base);
    width: 35%;
    height: 100vh;
    margin: 0 auto;
}

.discover {
    border: 1px solid var(--white-base);
    color: var(--white-base);
    border-radius: 10px;
    width: 10em;
    background-color: var(--black-base);
    height: 3em;
    transition: 0.4s;
    margin-top: 9vh;
    box-shadow: 0px 10px 15px 10px rgb(0 0 0 / 15%);
}

.discover:hover {
    background-color: var(--blue-base);
    transition: 0.4s;
    transform: translateY(-10px);
}

@keyframes myAnim {
	0%,
	100% {
		transform: translateY(0);
	}

	10%,
	30%,
	50%,
	70% {
		transform: translateY(-5px);
	}

	20%,
	40%,
	60% {
		transform: translateY(5px);
	}

	80% {
		transform: translateY(5.4px);
	}

	90% {
		transform: translateY(-5.4px);
	}
}

.discover-anim {
    animation: myAnim 3s linear 2s infinite alternate-reverse forwards;
}

/*ABOUT SECTION*/

.about {
    text-align: center;
    background-color:var(--black-base); 
    height: 100vh;
}

.main-about {
    background-color: var(--black-base);
    color: var(--white-base);
    z-index: 2;
    padding: 10px;
    margin: 20vh auto;
    width: 55em;
    position: relative;
    border-radius: 10px;
    border: 1px solid var(--white-base);
    display: flex;
    align-items: center;
    flex-direction: row;
    box-shadow: 0px 10px 15px 10px rgb(0 0 0 / 15%);
}

.main-left-side {
    width: 30%;
    padding: 20px;
}

.main-right-side {
    width: 70%;
    padding: 20px;
    text-align: left;
}

.profile-about {
    width: 150px;
    height: 150px;
    border-radius: 50%;
}

/*PROJECTS SECTION*/
#projects {
    height: 300vh;
    background-color: var(--blue-base);
    padding: 50px;
    text-align: center;
    color: var(--white-base);
}

.projects-wrapper {
    display: flex;
    flex-direction: row;
    align-items: center;
    gap: 0px;
    margin-bottom: 10vh;
}

.projects-item {
    width: 330px;
    height:520px ;
    background-color: var(--black-base);
    position: relative;
    z-index: 2;
    border-radius: 10px;
    margin: 0 auto;
    transition: 0.4s;
    border: 1px solid var(--black-base);
    box-shadow: 0px 10px 15px 10px rgb(0 0 0 / 15%);
}

.projects-item:hover {
    transform: translateY(-1em);
    transition: 0.4s;
    border: 1px solid var(--white-base);
}

.img-projects {
    width: 327px;
    height: 190px;
    border-top-left-radius: 10px;
    border-top-right-radius: 10px;
}

.projects-desc {
    margin: 20px;
    text-align: left;
}
/*ACHIEVEMENTS SECTION*/

.achievements {
    background-color: var(--black-base);
    padding: 55px;
    color: var(--white-base);
    text-align: center;
    height: 100vh;
}

.ach-row {
    display: flex;
    flex-direction:row;
    flex-wrap: nowrap;
    justify-content: center;
}

.ach-img {
    height: 330px;
    width: 500px;
    margin: 25px;
    border-radius: 7px;
    transition: 0.4s;
    position: relative;
    z-index: 2;
}

.ach-img:hover {
    transform: scale(1.12);
    cursor: pointer;
    transition: 0.4s;
}


/*CONTACT SECTION*/

.contact {
    background-color: var(--blue-base);
    padding: 55px;
    text-align: center;
    color: var(--white-base);
    height: 100vh;
}

.contact-wrapper {
    background-color: var(--black-base);
    color: var(--white-base);
    z-index: 2;
    padding: 10px;
    margin: 10vh auto;
    width: 55em;
    position: relative;
    border-radius: 10px;
    border: 1px solid var(--white-base);
    display: flex;
    align-items: center;
    flex-direction: row;
    box-shadow: 0px 10px 15px 10px rgb(0 0 0 / 15%);
}

.contact-desc {
    width: 30%;
    padding: 20px;
    text-align: left;
    border-right: 1px solid var(--white-base);
}

.contact-card {
    width: 70%;
    padding: 20px;
    margin: 0 auto;
}

.profile {
    border-radius: 50%;
    height: 110px;
    width: 110px;
}

.email-button {
    border: 1px solid var(--white-base);
    color: var(--white-base);
    border-radius: 10px;
    width: 10em;
    background-color: var(--black-base);
    height: 3em;
    transition: 0.4s;
    margin : 0 auto;
    display: block;
    text-align: center;
    padding: 10px;
}

.email-button:hover {
    background-color: var(--blue-base);
    color:var(--white-base);
}

.email-link {
    text-decoration: none;
    width: 100%;
}

.social-media-wrapper {
    display: flex;
    flex-direction: row;
    gap : 10;
    margin: 0 auto;
}

.img-logo-contact {
    filter: invert();
    width: 35px;
    height: 35px;
    margin: 9px;
    transition: 0.4s;
}

.img-logo-contact:hover {
    transform: translateY(-10px);
    transition: 0.4s;
}

.email-btn-img {
    width: 20px;
    height: auto;
}


/*FOOTER SECTION*/
footer {
    box-shadow: 0px 10px 15px 10px rgb(0 0 0 / 15%);
    background-color: var(--black-base); 
    padding: 25px;
    text-align: center;
    border-top: 2px solid var(--white-base);
    color: var(--white-base);
    z-index: 2;
    position: relative;
}

footer span {
    background-color: var(--blue-base);
    color: var(--white-base);
    padding: 5px;
    border-radius: 5px;
}

/*RESPONSIVE SECTION*/

@media screen and (max-width: 768px) {
    body, html {
        background-repeat: no-repeat;
        scroll-behavior: smooth;
        scroll-snap-type: none;
        margin: 0px;
        padding: 0px;
        overflow-x: hidden;
    }

    /*LANDING PAGE SECTION */
    .section-1 {
        height: 130vh;
        text-align: center;
        background-color: var(--blue-base);
    }
    
    .s1-wrapper {
        display: flex;
        flex-direction: column;
        gap: 0;
    }
    
    .s1-left-side {
        padding: 30px;
        color: var(--white-base);
        width: 100%;
        height: 70vh;
        margin: 0 auto;
        text-align: left;
    }
    
    .greeting {
        margin-top: 25vh;
    }
    
    .s1-right-side {
        padding: 10px;
        color: var(--white-base);
        width: 100%;
        height: 10vh;
        background-color: var(--blue-base);
        margin-bottom: 50vh;
    }
    
    .discover {
        border: 1px solid var(--white-base);
        color: var(--white-base);
        border-radius: 10px;
        width: 10em;
        background-color: var(--black-base);
        height: 3em;
        transition: 0.4s;
        z-index: 2;
    }

    /*NAVBAR SECTION*/
    .navbar-nav {
        padding: 1px;
    }
    .navbar-brand {
        color: var(--white-base);
        margin-left :1px;
        border: none;
    }

    /*ABOUT SECTION*/
    .about {
        height: 155vh;
    }
    .main-about {
        background-color: var(--black-base);
        color: var(--white-base);
        z-index: 2;
        padding: 10px;
        margin: 5vh auto;
        width: 22em;
        position: relative;
        border-radius: 10px;
        border: 1px solid var(--white-base);
        display: flex;
        align-items: center;
        flex-direction: column;
    }
    
    .main-left-side {
        width: 100%;
        padding: 20px;
    }
    
    .main-right-side {
        width: 100%;
        padding: 20px;
        text-align: left;
    }
    
    .profile-about {
        width: 120px;
        height: 120px;
        border-radius: 50%;
    }

    /**PROJECTS SECTION*/
    #projects {
        height: 850vh;
        background-color: var(--blue-base);
        padding: 50px 10px 10px 10px;
        text-align: center;
        color: var(--white-base);
    }
    
    .projects-wrapper {
        display: flex;
        flex-direction: column;
        align-items: center;
        gap: 10vh;
        margin-bottom: 10vh;
        margin : 10vh auto;
    }

    /*ACHIEVEMENTS SECTION*/
    .ach-row {
        display: block;
    }

    .achievements {
        background-color: var(--black-base);
        padding: 25px;
        color: var(--white-base);
        text-align: center;
        height: 120vh;
    }

    .ach-img {
        height: auto;
        width: 280px;
        margin: 25px auto;
        transition: 0.3s;
    }

    /*CONTACT SECTION*/

    .contact {
        padding: 35px 5px 35px 5px;
        text-align: center;
        color: var(--white-base);
    }

    .contact-wrapper {
        display: flex;
        gap: 20px;
        justify-content: center;
        flex-direction: column;
        background-color: var(--black-base);
        color: var(--white-base);
        z-index: 2;
        padding: 10px;
        margin: 5vh auto;
        width: 22em;
        position: relative;
        border-radius: 10px;
        border: 1px solid var(--white-base);
        display: flex;
        align-items: center;
        flex-direction: column;
    }
    
    .contact-desc {
        width: 100%;
        padding: 20px;
        border: none;
        text-align: left;
    }
    
    .contact-card {
        width: 100%;
        padding: 20px;
    }

    /*FOOTER SECTION*/
    footer {
        padding: 15px;
    }

}